@media (min-width: 768px){
  .md\:mt-0{
    margin-top: 0px;
  }

  .md\:mb-0{
    margin-bottom: 0px;
  }

  .md\:flex{
    display: flex;
  }

  .md\:grid{
    display: grid;
  }

  .md\:hidden{
    display: none;
  }

  .md\:w-48{
    width: 12rem;
  }

  .md\:w-auto{
    width: auto;
  }

  .md\:w-3\/5{
    width: 60%;
  }

  .md\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .md\:grid-cols-3{
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .md\:flex-row{
    flex-direction: row;
  }

  .md\:space-x-10 > :not([hidden]) ~ :not([hidden]){
    --tw-space-x-reverse: 0;
    margin-right: calc(2.5rem * var(--tw-space-x-reverse));
    margin-left: calc(2.5rem * calc(1 - var(--tw-space-x-reverse)));
  }

  .md\:rounded-3xl{
    border-radius: 1.5rem;
  }

  .md\:p-10{
    padding: 2.5rem;
  }

  .md\:text-left{
    text-align: left;
  }

  .md\:text-4xl{
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  .md\:backdrop-blur{
    --tw-backdrop-blur: blur(8px);
  }
}

